* {
  box-sizing: border-box;
}

.slider {
  height: 550px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  background-position: center center;
  background-size: cover;
  transition: 0.4s;
}

.slider-container {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  height: 100%;
  width: 100vw;
  position: relative;
  overflow: hidden;
  text-align: center;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide {
  opacity: 0;
}

.slide.active {
  width: 100%;
  height: 100%;
  background-position: center center;
  background-size: cover;
  position: absolute;
  transition: 0.4s ease;
  z-index: 1;
  overflow: hidden;
  opacity: 1;
  background-color: #012854;
}

.arrow {
  position: absolute;
  background-color: transparent;
  color: #fff;
  padding: 20px;
  font-size: 30px;
  border: none;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 5;
}

.arrow:focus {
  outline: 0;
  background-color: transparent;
}

.left-arrow {
  left: calc(7vw - 65px);
}

.right-arrow {
  right: calc(7vw - 65px);
}

.slide-indicators {
  position: absolute;
  text-align: center;
  top: 520px;
  z-index: 5;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #ccc;
  display: inline-block;
  margin: 0 6px;
  cursor: pointer;
}

.indicator.active {
  background: #143b65;
}

/*imagenes del slider */
.familia,
.sonrisa,
.doctores,
.vision {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -2;
}

.familia {
  clip-path: polygon(0 0, 65% 0, 100% 100%, 0 100%);
}

.sonrisa {
  clip-path: circle(58.3% at 79% 51%);
}

.doctores {
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
}

.vision {
  clip-path: circle(49.9% at 50% 50%);
}

